body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box; }

body {
  background: url(/Static-Content-Website/static/media/background.1a673e60.png);
  background-size: cover;
  font-size: 16px;
  height: 100vh; }

::-webkit-scrollbar {
  width: 1em; }

::-webkit-scrollbar-track {
  background: linear-gradient(to right, #201c29, #201c29 1px, #100e17 1px, #100e17); }

::-webkit-scrollbar-thumb {
  background: linear-gradient(to bottom, #ff8a00, #da1b60);
  border-radius: 30px;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, 0.25), inset -2px -2px 2px rgba(0, 0, 0, 0.25); }

::-moz-selection {
  background: #9c27b0;
  color: #fff; }

::selection {
  background: #9c27b0;
  color: #fff; }

::-moz-selection {
  background: #9c27b0;
  color: #fff; }

#root {
  height: 100%; }

#main {
  display: flex;
  width: 75%;
  max-width: 1500px;
  height: 100%;
  margin: 0 auto;
  padding: 50px 0; }

#main:after {
  position: absolute;
  background-color: #343838;
  opacity: 0.5;
  content: "";
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
  width: 75%;
  margin: 0 auto; }

#menu {
  flex: 0 0 250px; }
  #menu .item {
    cursor: pointer;
    font-size: 1em;
    padding: 15px 30px;
    transition: all 250ms; }
    #menu .item:hover {
      padding: 15px 45px;
      background-color: rgba(246, 246, 246, 0.2); }

ul {
  height: 100%;
  list-style-type: none;
  padding: 10px;
  border-right: solid purple thin;
  overflow-y: auto; }

.li:active, li:hover {
  color: lightblue; }

.active {
  background: url(/Static-Content-Website/static/media/arrow.a71a8e79.png) no-repeat left center;
  background-size: 18px 18px;
  padding-left: 45px !important; }

#content {
  flex: 1 1;
  font-size: 1em;
  padding: 0 40px; }

code {
  background: linear-gradient(to right, #fce4ed, #ffe8cc); }

